.card {
	background-color: rgb(34, 197, 94);
	margin: 0.5rem;
	display: inline-block;
	border-radius: 0.25rem;
	border-width: 2px;
	padding: 0.5rem;
	text-align: center;
	box-shadow: 0 0 10px white;
	height: 350px;
	width: 300px;
}

.card-list {
  text-align: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

@font-face {
	font-family: "SEGA LOGO FONT";
	font-style: normal;
	font-weight: normal;
	src: local("SEGA LOGO FONT"), url(/robofriends/static/media/SEGA.12a3db8b.woff) format("woff");
}

h1 {
	font-family: "SEGA LOGO FONT";
	font-weight: 200;
	color: #0ccac4;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(63, 133, 242);
  background: linear-gradient(
    to right,
    rgba(63, 133, 242, 1) 0%,
    rgba(68, 68, 237, 1) 25%,
    rgba(0, 40, 48, 1) 100%
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

